
import { deleteArticle, fetchArticles } from "@/api";
import { IArticle } from "@/typings";
import { defineComponent, onMounted, reactive, ref, toRefs } from "vue";
import Modal from "@/components/Modal/index.vue";
import $message from "@/components/Message";
import useDayjs from "@/hooks/useDayjs";
import ArticleList from "@/components/List/ArticleList.vue";

export default defineComponent({
  name: "PostsDashboard",
  components: {
    Modal,
    ArticleList,
  },
  setup() {
    const articleList = ref<IArticle[]>([]);
    const state = reactive({
      dialogDel: false,
      deleteId: "",
    });

    const { fromNow } = useDayjs();

    const getArticles = async () => {
      const { data } = await fetchArticles();
      articleList.value = data.reverse();
    };

    const del = (article: IArticle) => {
      state.dialogDel = true;
      state.deleteId = article._id;
    };

    const confirmDel = async () => {
      try {
        const { status } = await deleteArticle(state.deleteId);
        if (status === 204) {
          $message({
            type: "success",
            message: "删除成功",
          });
        }
        getArticles();
      } catch (error) {
        //
      }
      closeDel();
    };

    const closeDel = () => {
      state.dialogDel = false;
      state.deleteId = "";
    };

    onMounted(() => {
      getArticles();
    });

    return {
      ...toRefs(state),
      articleList,
      del,
      confirmDel,
      closeDel,
      fromNow,
    };
  },
});
