
import useCreateDOM from "@/hooks/useCreateDOM";
import { defineComponent } from "vue";

export default defineComponent({
  name: "Modal",
  props: {
    title: String,
    body: String,
    isVisible: {
      type: Boolean,
      default: false,
    },
  },
  emits: ["modal-close", "modal-confirm"],
  setup(props, { emit }) {
    useCreateDOM("modal");

    const close = () => {
      emit("modal-close");
    };

    const confirm = () => {
      emit("modal-confirm");
    };

    return {
      close,
      confirm,
    };
  },
});
